@font-face {
  font-family: "Roboto-light";
  src: url(../Fonts/Roboto-Light.ttf) format("truetype");
}
@font-face {
  font-family: "Roboto-bold";
  src: url(../Fonts/Roboto-Bold.ttf) format("truetype");
}
@font-face {
  font-family: "Roboto-regular";
  src: url(../Fonts/Roboto-Regular.ttf) format("truetype");
}
.App {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  flex-wrap: nowrap;
  /* Optional: show position indicator in red */
}
.App ::-webkit-scrollbar {
  width: 0px;
  background: none;
}
.App ::-webkit-scrollbar-thumb {
  background: #fff;
}

.DairyAppTitle {
  width: 270px;
  height: calc(100vh - 30px);
  padding: 30px 0 0 0;
  color: #fff;
  background-color: #2B2F3E;
}

.CustomHeader {
  width: 100%;
  font-size: 36px;
  font-family: Roboto-bold, sans-serif;
}
.CustomHeader::first-letter {
  margin: 0 0 0 25px;
}

.CustomTitle {
  width: 100%;
  font-family: Roboto-light, sans-serif;
  font-size: 18px;
  color: #79797B;
}
.CustomTitle::first-letter {
  margin: 0 0 0 25px;
}

.Tasks {
  width: 440px;
  height: 620px;
  margin: 50px 0 0 50px;
  box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.25), -2px 0px 5px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  overflow: auto;
  /* Optional: show position indicator in red */
}
.Tasks::-webkit-scrollbar {
  width: 0px;
  background: none;
}
.Tasks::-webkit-scrollbar-thumb {
  background: #fff;
}

.TaskTitle {
  margin: 35px 0 22px 0;
  color: #565A69;
}

.NewTasksForm {
  width: 100%;
  height: 35px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-start;
}

.SaveTaskBtn {
  width: 120px;
  height: 35px;
  border: 1px solid #27CCC0;
  border-radius: 5px;
  margin: 0 20px 0 auto;
  font-family: Roboto-regular, sans-serif;
  font-size: 16px;
  color: #fff;
  cursor: pointer;
  background-color: #27CCC0;
  transition: 0.2s;
}
.SaveTaskBtn:focus {
  color: #000;
  border: 1px solid #000;
  outline: none;
}

.NewTaskInput {
  width: calc(250px - 13px);
  height: 35px;
  padding: 0 0 0 11px;
  margin: 0 21px 0 25px;
  border: 1px solid #CCCCCC;
  border-radius: 5px;
}
.NewTaskInput:focus {
  outline: none;
  border: 1px solid #27CCC0;
}
.NewTaskInput::-webkit-input-placeholder {
  color: #979797;
  font-family: Roboto-light, sans-serif;
  font-size: 16px;
}
.NewTaskInput:-ms-input-placeholder {
  color: #979797;
  font-family: Roboto-light, sans-serif;
  font-size: 16px;
}
.NewTaskInput::placeholder {
  color: #979797;
  font-family: Roboto-light, sans-serif;
  font-size: 16px;
}

.TaskItem {
  width: calc(100% - 25px);
  height: 35px;
  margin: 11px 0 0 0;
  padding: 0 0 0 25px;
  font-size: 15px;
  font-family: Roboto-bold, sans-serif;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
}

.TaskActive {
  padding: 0 0 0 19px;
  border-left: 6px solid #FF2F5A;
}

.TaskName {
  min-width: calc(90px - 11px);
  max-width: calc(270px - 11px);
  height: 35px;
  padding: 0 0 0 11px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-start;
  overflow: hidden;
  font-size: 15px;
  font-family: Roboto-regular, sans-serif;
}

.NumberedDot {
  min-width: 30px;
  height: 25px;
  margin: 0 auto 0 20px;
  color: #fff;
  font-family: Roboto-light, sans-serif;
  font-size: 14px;
  border: 1px solid #27CCC0;
  border-radius: 50px;
  background-color: #27CCC0;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.DeleteBtn {
  width: 98px;
  height: 33px;
  margin: 0 20px 0 0;
  border: 1px solid #FF3159;
  border-radius: 5px;
  outline: none;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-family: Roboto-regular, sans-serif;
  font-size: 15px;
  color: #FF3159;
  cursor: pointer;
}
.DeleteBtn:hover {
  color: #fff;
  background-color: #FF3159;
}

.AvatarPickerSection {
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-around;
}

.AvatarItem {
  width: 48px;
  height: 48px;
  border-radius: 5px;
  border: 1px solid transparent;
  cursor: pointer;
}
.AvatarItem:hover {
  border: 1px solid #27CCC0;
}

.CommentsSection {
  width: 500px;
  height: 620px;
  margin: 50px 0 0 100px;
  box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.25), -2px 0px 5px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.CommentsTitle {
  margin: 35px 0 22px 0;
  color: #565A69;
}

.CommentsContainer {
  width: 100%;
  min-height: 100px;
  margin: 0 0 auto 0;
  overflow: auto;
  /* Optional: show position indicator in red */
}
.CommentsContainer::-webkit-scrollbar {
  width: 0px;
  background: none;
}
.CommentsContainer::-webkit-scrollbar-thumb {
  background: #fff;
}

.CommentsTextSection {
  width: 100%;
  height: 50px;
  margin: 50px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.AvatarComponent {
  width: 50px;
  height: 50px;
  margin: 0 0 0 22px;
  border: 1px solid #CCCCCC;
  border-radius: 5px;
  cursor: pointer;
  background-color: transparent;
}
.AvatarComponent:hover {
  border: 1px solid #27CCC0;
}
.AvatarComponent:focus {
  outline: none;
  border: 1px solid #27CCC0;
}

.CommentsTextArea {
  width: 75%;
  height: 38px;
  margin: 0 22px 0 0;
  padding: 5px;
  font-family: Roboto-regular, sans-serif;
  font-size: 16px;
  border: 1px solid #CCCCCC;
  border-radius: 5px;
}
.CommentsTextArea:focus {
  outline: none;
  border: 1px solid #27CCC0;
}

.CommentItem {
  width: 90%;
  margin: 10px auto 0 auto;
  padding: 0 0 10px 0;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  flex-wrap: nowrap;
  justify-content: space-between;
  border-bottom: 1px solid #CCCCCC;
}

.SavadAvatar {
  width: 50px;
  height: 50px;
  border-radius: 3px;
}

.commentText {
  width: 360px;
  height: auto;
  font-size: 15px;
  font-family: Roboto-regular, sans-serif;
  overflow: hidden;
  /* Optional: show position indicator in red */
}
.commentText::-webkit-scrollbar {
  width: 0px;
  background: none;
}
.commentText::-webkit-scrollbar-thumb {
  background: #fff;
}
.commentText::first-letter {
  text-transform: uppercase;
}